.container {
  position: relative;
  width: 100%;
  height: 100%;
  padding: 2rem;
  transition: all 0.5s cubic-bezier(0.16, 1, 0.3, 1);
  overflow: hidden;
  background: white;
}

.container.fullView {
  padding: 4rem;
}

.content {
  max-width: 1400px;
  margin: 0 auto;
  height: 100%;
  display: flex;
  flex-direction: column;
}

.caption {
  font-size: 0.75rem;
  letter-spacing: 0.2em;
  text-transform: uppercase;
  margin-bottom: 1rem;
}

.title {
  font-size: 3rem;
  font-weight: 400;
  margin-bottom: 2rem;
  transform: translateY(0);
  transition: font-size 0.3s ease, transform 0.3s ease;
}

.container.fullView .title {
  font-size: 4rem;
  transform: translateY(-10%);
}

.articleRow {
  display: flex;
  gap: 2rem;
  overflow-x: auto;
  padding-bottom: 1rem;
  flex: 1;
  align-items: center;
}

.articleItem {
  min-width: 200px;
  flex-shrink: 0;
  transition: transform 0.3s ease;
}

.container.fullView .articleItem {
  min-width: 300px;
  transform: scale(1.2) translateY(-10px);
}

.imageWrapper {
  position: relative;
  width: 100%;
  height: 0;
  padding-bottom: 100%;
  margin-bottom: 1rem;
  overflow: hidden;
}

.image {
  object-fit: cover;
  transition: transform 0.3s ease;
}

.imageWrapper:hover .image {
  transform: scale(1.05);
}

.articleTitle {
  font-size: 0.75rem;
  letter-spacing: 0.1em;
  text-transform: uppercase;
}

@media (max-width: 768px) {
  .container {
    padding: 1.5rem;
  }

  .title {
    font-size: 2rem;
  }

  .articleRow {
    flex-direction: column;
    overflow-x: visible;
    gap: 1rem;
  }

  .articleItem {
    width: 100%;
    min-width: auto;
  }

  .container.fullView .articleItem {
    min-width: auto;
  }
}
